body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

/* The location pointed to by the popup tip. */
.popup-tip-anchor {
  height: 0;
  position: absolute;
  /* The max width of the info window. */
  width: 150px;
}
/* The bubble is anchored above the tip. */
.popup-bubble-anchor {
  position: absolute;
  width: 100%;
  bottom: /* TIP_HEIGHT= */ 8px;
  left: 0;
}
/* Draw the tip. */
.popup-bubble-anchor::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  /* Center the tip horizontally. */
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  /* The tip is a https://css-tricks.com/snippets/css/css-triangle/ */
  width: 0;
  height: 0;
  /* The tip is 8px high, and 12px wide. */
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: /* TIP_HEIGHT= */ 8px solid white;
}


.input {
  /* Box Model */
  height: 30px;
  width: 100%;

  /* Visual */
  outline: none;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  background: transparent;

  /* Typography */
  font-size: 1em;
  color: #333;
}

.input:focus {
  /* Visual */
  border-color: cornflowerblue;
}

.main-form {
  /* Position */
  position: absolute;
  top: 80px;
  left: 10px;
  z-index: 10;

  /* Box Model */
  min-width: calc(100vw - 20px);
  padding: 10px;
  display: flex;

  /* Visual */
  background-color: #fff;
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  border-radius: 4px;
}

.main-form__place-input {
  /* Box Model */
  width: 100%;
  
  /* Typography */
  font-size: 1.2em;
}

.main-form__submit {
  /* Box Model */
  margin-left: 10px;

  /* Visual */
  background: transparent;
  border: none;
  cursor: pointer;

  /* Typography */
  font-size: 1.5em;
  color: #666;
}

.main-form__submit:focus, .main-form__submit:hover {
  /* Typography */
  color: cornflowerblue;
}


@media only screen and (min-width: 768px) {
  .main-form {
    width: 450px;
    min-width: 0px;
  }
}

.map {
  /* Position */
  position: absolute;
  top: 0;
  left: 0;

  /* Box Model */
  width: 100%;
  height: 100%;

  /* Visual */
  background-color: antiquewhite;
}

.marker-details {
  /* Position */
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: translate(-50%, -100%);
          transform: translate(-50%, -100%);

  /* Box Model */
  height: 150px;
  padding-bottom: 5px;

  /* Visual. */
  background-color: white;
  border-radius: 5px;
  overflow-y: auto;
  box-shadow: 0px 2px 10px 1px rgba(0, 0, 0, 0.5);
}

.marker-details__cover {
  /* Position */
  position: relative;

  /* Box Model */
  width: 150px;
  height: 60px;
}

.marker-details__cover::after {
  /* Position */
  position: absolute;
  bottom: 0;
  left: 0;

  /* Box Model */
  height: 100%;
  width: 100%;

  /* Visual */
  content: "";
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
}

.marker-details__cover img {
  /* Box Model */
  width: 100%;
  height: 100%;

  /* Visual */
  object-fit: cover;
}

.marker-details__cover h1 {
  /* Position */
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;

  /* Box Model */
  margin: 0;

  /*  Visual */
  overflow: hidden;

  /* Typography */
  font-size: 2em;
  color: #fff;
  white-space: pre-line;
  text-overflow: ellipsis;
}

.marker-details__rating {
  /* Box Model */
  display: flex;
  align-items: center;
}

.rating {
  /* Position */
  position: relative;

  /* Box Model */
  display: inline-block;
  width: 5rem;
  height: 1rem;

  /* Typography */
  font-size: 1rem;
  line-height: 1rem;
}

.rating div {
  /* Position */
  position: absolute;
  top: 0;
  left: 0;

  /* Box Model */
  width: 100%;
}

.rating--colored {
  /* Position */
  z-index: 1;
  
  /* Visual */
  overflow: hidden;

  /* Typography */
  color: #e7711b;
}

.place-details {
  /* Position */
  position: absolute;
  top: 200px;
  z-index: 10;

  /* Box Model */
  display: flex;
  flex-direction: column;
  height: calc(100vh - 200px);
  width: 100%;

  /* Visual */
  -webkit-transition: -webkit-transform .5s ease-in-out;
  transition: -webkit-transform .5s ease-in-out;
  transition: transform .5s ease-in-out;
  transition: transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
  overflow-y: auto;
}

.place-details__close {
  /* Position */
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 10;

  /* Visual */
  background: transparent;
  border: none;
  cursor: pointer;

  /* Typography */
  color: #fff;
  font-size: 24px;
}

.place-details__cover {
  /* Position */
  position: -webkit-sticky;
  position: sticky;
  top: 0;

  /* Box Model */
  display: flex;
  align-items: flex-end;
  width: 100%;
  min-height: 100px;

  /* Visual */
  background-color: cornflowerblue;
  background-size: cover;
  background-position: center;
}

.place-details__cover::after {
  /* Position */
  position: absolute;
  bottom: 0;
  left: 0;

  /* Box Model */
  height: 100%;
  width: 100%;

  /* Visual */
  content: "";
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
}

.place-details__cover img {
  /* Position */
  position: absolute;
  top: 0;

  /* Box Model */
  width: 100%;
  height: 100%;

  /* Visual */
  object-fit: cover;
}

.place-details__cover h1 {
  /* Position */
  z-index: 1;

  /* Box Model */
  margin: 0;
  margin-left: 6px;
  margin-bottom: 6px;
  padding-top: 30px;

  /*  Visual */
  overflow: hidden;

  /* Typography */
  font-size: 2em;
  color: #fff;
  white-space: pre-line;
  text-overflow: ellipsis;
}

.place-details__content {
  /* Box Model */
  flex-grow: 1;

  /* Visual */
  background-color: #fff;
  overflow-y: auto;
}

.place-details__content section {
  /* Box Model */
  padding: 12px 6px;
  
  /* Typography */
  color: #333;
}

.place-details__rating {
  /* Box Model */
  display: flex;
  align-items: center;
}

.place-details__wikipedia {
  /* Box Model */
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  padding: 12px 6px;

  /* Typography */
  color: #333;
}

.place-details__animation-enter {
  -webkit-transform: translateY(100vh);
          transform: translateY(100vh);
  -webkit-transform: translate3d(0, 100vh, 0);
          transform: translate3d(0, 100vh, 0);
}

.place-details__animation-enter-active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.place-details__animation-exit {
  -webkit-transform: translateY(100vh);
          transform: translateY(100vh);
  -webkit-transform: translate3d(0, 100vh, 0);
          transform: translate3d(0, 100vh, 0);
}


@media only screen and (min-width: 768px) {
  .place-details {
    /* Position */
    position: absolute;
    top: 0px;
    right: 0;
    z-index: 10;
  
    /* Box Model */
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 35%;
  
    /* Visual */
    -webkit-transition: -webkit-transform .5s ease-in-out;
    transition: -webkit-transform .5s ease-in-out;
    transition: transform .5s ease-in-out;
    transition: transform .5s ease-in-out, -webkit-transform .5s ease-in-out;
    overflow-y: auto;
  }
  

  .place-details__animation-enter {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
  
  .place-details__animation-enter-active {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  
  .place-details__animation-exit {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

